.about-wrapper {
	display: grid;
	grid-template-columns: 60% 40%;
	h2 {
		grid-column: 1 / 2;
		grid-row: 1;
		justify-self: center;
		align-self: center;
	}
	.text-wrapper {
		grid-template-columns: auto;
		grid-template-rows: auto auto;
		padding-top: 80px;
		padding-bottom: 100px;
		padding-left: 60px;
		padding-right: 60px;
		margin-top: 120px;
		background-color: #E9C3B8;
		h3 {
			grid-row: 1 / 2;
			color: #000;
			padding-bottom: 30px;
		}
		p {
			grid-row: 2 / 3;
			font-size: 20px;
			color:  #606060;
			.bolded {
				font-weight: 700;
				color: #000;
			}
		}
	}
}

.skills-wrapper {
	display: grid;
	grid-template-columns: 70% 30%;
	h2 {
		grid-column: 2 / 3;
		grid-row: 1;
		justify-self: center;
		align-self: center;
	}
	.technics-wrapper {
		background-color: #F1F1F1;
		grid-column: 1 / 2;
		grid-row: 1;
		display: grid;
		grid-template-columns: 33.3% 33.3% 33.3%;
		grid-template-rows: 25% 25% 25% 25%;
		margin-left: 50px;
		margin-right: 50px;
		.item-wrapper:nth-child(1) {
			grid-column: 1 / 2;
			grid-row: 1 / 2;
		}
		.item-wrapper:nth-child(2) {
			grid-column: 2 / 3;
			grid-row: 1 / 2;
		}
		.item-wrapper:nth-child(3) {
			grid-column: 3 / 4;
			grid-row: 1 / 2;
		}
		.item-wrapper:nth-child(4) {
			grid-column: 1 / 2;
			grid-row: 2 / 3;
		}
		.item-wrapper:nth-child(5) {
			grid-column: 2 / 3;
			grid-row: 2 / 3;
		}
		.item-wrapper:nth-child(6) {
			grid-column: 3 / 4;
			grid-row: 2 / 3;
		}
		.item-wrapper:nth-child(7) {
			grid-column: 1 / 2;
			grid-row: 3 / 4;
		}
		.item-wrapper:nth-child(8) {
			grid-column: 2 / 3;
			grid-row: 3 / 4;
		}
		.item-wrapper:nth-child(9) {
			grid-column: 3 / 4;
			grid-row: 3 / 4;
		}
		.item-wrapper:nth-child(10) {
			grid-column: 2 / 3;
			grid-row: 4 / 5;
		}
		.img-wrapper {
			text-align: center;
			img {
				max-width: 200px;
				height: 80px;
				padding-top: 30px;
				padding-bottom: 10px;
			}
		}
		.item-wrapper {
			text-align: center;
			position: relative;
			cursor: pointer;
			padding-top: 30px;
			padding-bottom: 30px;
			h3 {
				display: inline-block;
				font-size: 18px;
			}
			.text-wrapper {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				opacity: 0;
				transition: height .3s ease-in-out, opacity .4s ease-in-out;
				display: grid;
			}
			&:hover .text-wrapper,  &:focus .text-wrapper {
				cursor: pointer;
				opacity: .9;
				height: 100%;
				background-color: #000;
				color: #fff;
				padding-left: 20px;
				padding-right: 20px;
			}
			&:hover .text-wrapper .text {
				align-self: center;
				justify-self: center;
			}
			.enlarge {
				height: 150%;
				width: 150%;
			}
		}

		.md-modal {
			position: fixed;
			top: 50%;
			left: 50%;
			width: 50%;
			max-width: 630px;
			min-width: 320px;
			height: auto;
			z-index: 2000;
			visibility: hidden;
			transform: translateX(-50%) translateY(-50%);
			filter: blur(0.3px);
			zoom: 0.9;
			.md-content {
				color: #000;
				background-color:#fff;
				position: relative;
				border-radius: 3px;
				margin: 0 auto;
				h3 {
					margin: 0;
					padding-top: 30px;
					padding-bottom: 15px;
					text-align: center;
					font-size: 22px!important;
					font-weight: 300;
					opacity: 1;
					background-color: rgba(105, 87, 83, .9);
					display: block!important;
					color: #fff;
					border-radius: 3px 3px 0 0;
				}
				div {
					font-family: RobotoRegular;
					padding: 15px 40px 30px;
					margin: 0;
				}
				.md-close {
					z-index: 1;
					max-height: 40px;
					font-family: RobotoRegular;
					font-size: 18px;
					padding: 10px 20px;
					border: none;
					border-radius: 5px;
					background-color: rgba(105, 87, 83, 1);
					color: #fff;
					position: relative;
					overflow: hidden;
					cursor: pointer;
					&:before {
						content: '';
						z-index: -1;
						position: absolute;
						transform-origin: 100% 50%;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						width: 0px;
						height: 0px;
						opacity: 0;
						border-radius: 50%;
						background-color: #382F2D;
						transition: width .4s, height .4s, opacity .4s;
						transition-timing-function: cubic-bezier(0.7,0,0.9,1);
					}
					&:hover::before {
						height: 200px;
						width: 200px;
						opacity: 1;
					}
				}
			}
		}
		.md-overlay {
			position: fixed;
			width: 100%;
			height: 100%;
			visibility: hidden;
			top: 0;
			left: 0;
			z-index: 1000;
			opacity: 0;
			background: rgba(0, 0, 0, 0.5);;
			transition: all 0.3s;
		}
	}
}

.work-wrapper {
	display: grid;
	grid-template-columns: 35% 65%;
	h2 {
		justify-self: center;
		align-self: center;
		padding-right: 15px;
	}
	.items-wrapper {
		display: grid;
		grid-template-columns: 100%;
		grid-template-rows: auto auto;
		padding-left: 50px;
		padding-right: 50px;
		padding-top: 70px;
		padding-bottom: 60px;
		background-color: #DDEDEC;
		.intro {
			padding-bottom: 30px;
		}
		.item-wrapper {
			display: grid;
			grid-template-columns: 50% 50%;
			grid-template-rows: 100%;
			.content-wrapper {
				grid-column: 1 / 2;
				grid-row: 1 / 2;
				display: grid;
				grid-template-columns: 100%;
				grid-template-rows: auto auto auto;
				h3 {
					grid-column: 1 / 2;
					grid-row: 1 / 2;
					text-align: left;
					padding-bottom: 15px;
					font-size: 20px;
				}
				p {
					grid-column: 1 / 2;
					grid-row: 2 / 3;
					color: #555;
					font-size: 20px;
					text-align: left;
					padding-bottom: 10px;
				}
				button {
					grid-column: 1 / 2;
					grid-row: 3 / 4;
					max-height: 40px;
					max-width: 150px;
					justify-self: start;
					position: relative;
					z-index: 1;
					width: 100px;
					font-family: RobotoRegular;
					font-size: 18px;
					padding-top: 7px;
					padding-bottom: 7px;
					border: none;
					border-radius: 5px;
					overflow: hidden;
					color: #fff;
					background-color: #17a2b8;
					cursor: pointer;
					&:before {
						content: '';
						z-index: -1;
						position: absolute;
						transform-origin: 100% 50%;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						width: 0px;
						height: 0px;
						opacity: 1;
						border-radius: 50%;
						background-color: #138496;
						transition: width .4s, height .4s, opacity .4s;
						-webkit-transition-timing-function: cubic-bezier(0.7,0,0.9,1);
						transition-timing-function: cubic-bezier(0.7,0,0.9,1);
					}
					&:hover::before {
						height: 200px;
						width: 200px;
						opacity: 1;
					}
					.github {
						display: inline-block;
						width: 30px;
						height: 20px;
						padding-left: 5px;
					}
				}
				.img-wrapper-alternative {
					display: none;
				}
			}
			.img-wrapper {
				grid-column: 2 / 3;
				grid-row: 1 / 2;
				padding-bottom: 15px;
				justify-self: end;
				img {
					max-width: 300px;
					padding-left: 50px;
				}
			}
		}
	}
	.slick-arrow .slick-prev {
		display: none;
	}
}

.contact-wrapper {
	display: grid;
	grid-template-columns: 100%;
	grid-template-rows: auto auto;
	h2 {
		grid-column: 1 / 3;
		grid-row: 1 / 2;
		justify-self: center;
		align-self: center;
		margin-top: 50px;
		margin-bottom: 60px;
	}
	.contact-form-content {
		grid-column: 1 / 3;
		grid-row: 2 / 3;
		display: grid;
		grid-template-columns: 50% 50%;
		grid-template-rows: auto;
		.form-wrapper {
			grid-column: 1 / 2;
			grid-row: 1 / 2;
			background-color: #695753;
			padding-top: 60px;
			padding-bottom: 50px;
			margin-bottom: 40px;
			padding-left: 30px;
			padding-right: 30px;
			h3 {
				font-family: RobotoLight;
				font-size: 20px;
				color: #fff;
				padding-bottom: 20px;
			}
			.intro {
				padding-bottom: 40px;
			}
			form {
				display: grid;
				grid-template-columns: auto;
				grid-template-rows: auto auto auto auto auto;
				font-size: 15px;
				text-align: left;
				div input, input {
					color: #555;
					font-size: 18px;
					padding-left: 15px;
					padding-right: 15px;
					padding-top: 10px;
					padding-bottom: 10px;
					margin-bottom: 15px;
					min-width: 100%;
					max-width: 100%;
					border-radius: 5px;
					outline: none;
					border: none;
				}
				input::placeholder, input::-webkit-input-placeholder, input:-moz-placeholder,
				input::-moz-placeholder, input:-ms-input-placeholder,
				textarea::placeholder, textarea::-webkit-input-placeholder, textarea:-moz-placeholder,
				textarea::-moz-placeholder, textarea:-ms-input-placeholder {
					font-size: 18px;
					color: #555;
					padding-left: 5px;
				}
				textarea {
					font-size: 18px;
					color: #555;
					font-family: RobotoRegular;
					padding-left: 15px;
					padding-top: 15px;
					min-width: 100%;
					max-width: 100%;
					resize: none;
					outline: none;
					border: none;
					border-radius: 5px;
				}
				button {
					max-width: 150px;
					justify-self: start;
					position: relative;
					border: none;
					margin-top: 25px;
					padding: 10px 15px;
					border-radius: 5px;
					z-index: 1;
					font-size: 18px;
					font-family: RobotoRegular;
					color: #fff;
					background-color: #17a2b8;
					overflow: hidden;
					cursor: pointer;
					&:before {
						content: '';
						z-index: -1;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						width: 0px;
						height: 0px;
						opacity: 0;
						border-radius: 50%;
						background-color: #138496;
						transition: width .4s, height .4s, opacity .4s;
						-webkit-transition-timing-function: cubic-bezier(0.7,0,0.9,1);
						transition-timing-function: cubic-bezier(0.7,0,0.9,1);
					}
					&:hover::before {
						height: 200px;
						width: 200px;
						opacity: 1;
					}
				}
				.nameError, .emailError {
					margin-bottom: 10px;
				}
				.subject {
					margin-bottom: 25px;
				}
				.tooltiptext {
					color: #fff;
				}
				.message-sent {
					color: #fff;
					margin-top: 15px;
				}
			}
		}
		.pic-wrapper {
			justify-self: start;
			align-self: start;
			grid-column: 2 / 3;
			grid-row: 1 / 2;
			background-color: #555;
			img {
				max-width: 100%;
				height: auto;
				display: block;
			}
		}
	}
}


